import { Component, ViewChild   } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ActionSheetController, Platform } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '../../../node_modules/@angular/forms';
import { Developments,  Properties, Inputs, User, Api } from '../../providers';
import { Camera } from '@ionic-native/camera';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { AlertController } from 'ionic-angular';
import { AndroidPermissions  } from '@ionic-native/android-permissions';

@IonicPage()
@Component({
  selector: 'page-create-property',
  templateUrl: 'create-property.html',
})

export class CreatePropertyPage {

  @ViewChild('fileInput') public fileInput;

  public isReadyToSave  : boolean;
  public propertyImages          : FormGroup;
  public propertyGeneral       : FormGroup;
  public propertyPrices: FormGroup;
  public propertyLocation: FormGroup;
  public propertyInterior: FormGroup;
  public propertyDetail: FormGroup;
  public requiredLabelml = '';
  public requiredLabelm2c = '';


  public data           : any = [];
  public success        : boolean = false;
  public form           : boolean = true;
  public edit           : boolean = false;
  public msg            : String = '';
  public propertyid     : any;
  public gallery        : any = [];
  public alertProperty  : any;
  public filename       : any;
  public icon           : any;
  public update         : boolean = false;

  public view2  : { color: String, text: String, spinner: boolean, icon: boolean } = { color: 'b4', text: 'Seleccionar imagen', spinner: false, icon: false };
  public icons: { panel1: boolean, panel2: boolean, panel3: boolean, panel4: boolean, panel5: boolean, panel6: boolean, panel7: boolean } 
              = { panel1: false   , panel2: false, panel3: false, panel4: false, panel5: false, panel6: false, panel7: false };

  public color: { panel1: String, panel2: String, panel3: String, panel4: String, panel5: String, panel6: String } 
    = { panel1: 're3', panel2: 're3', panel3: 're3', panel4: 're3', panel5: 're3', panel6: 're3'};


  public text   : { panel1: String, panel2: String, panel3: String } = { panel1: 'Guardar', panel2: 'Guardar', panel3: 'Guardar' };

  public spinner: { panel1: boolean, panel2: boolean, panel3: boolean, panel4: boolean, panel5: boolean, panel6: boolean, panel7: boolean} 
                = { panel1: false, panel2: false, panel3: false, panel4: false, panel5: false, panel6: false, panel7: false  };
      
  public view : { panel1: boolean, panel2: boolean, panel3: boolean, panel4: boolean, panel5: boolean, panel6: boolean, panel7: boolean  } 
              = { panel1: true, panel2: false, panel3: false, panel4: false, panel5: false, panel6: false, panel7: false };

  
  constructor(public platform: Platform, public androidPermissions: AndroidPermissions , public api : Api, public alertCtrl: AlertController, private user: User, public camera: Camera,public params: NavParams, private actionSheetCtrl: ActionSheetController,private transfer: FileTransfer,public viewCtrl: ViewController, public properties: Properties, public inputs: Inputs, public development : Developments, public formBuilder: FormBuilder, public navCtrl: NavController, public navParams: NavParams) {

    platform.ready().then(() => {
      if(platform.is('android')){
        androidPermissions.requestPermissions(
          [
            androidPermissions.PERMISSION.CAMERA,
            androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE
          ]
        );
      }
    

    })

   



    this.propertyGeneral = formBuilder.group({
      tipoProp: ['', [Validators.required]],      
      propiedad: ['', [Validators.required]],        
      webEsp: ['', [Validators.required]],    
      onlineW: [''],
      keyword: ['', [Validators.required]] ,
      office: ['', [Validators.required]] ,
      agent: ['', [Validators.required]] 
    });



   

    this.propertyLocation = formBuilder.group({

      city: ['', [Validators.required]],
      state: ['', [Validators.required]],
      suburb: ['', [Validators.required]],
      township: ['', [Validators.required]],
      direccion: ['']

    });

    this.propertyInterior = formBuilder.group({
      recamaras: ['', [Validators.required]],
      banios: ['', [Validators.required]],
      mediobanios: ['', [Validators.required]]

    });

    this.propertyDetail = formBuilder.group({
      m2c: ['', [Validators.required]],
      ml: ['', [Validators.required]],     
      cajones: ['']
    });

    this.propertyImages = formBuilder.group({
      profilePic: ['']
    });

    this.propertyImages.valueChanges.subscribe((v) => {
      this.isReadyToSave = this.propertyImages.valid;
    });

    //Actualizar propiedad
    if (navParams.data > 0) {
      this.update = true;
      this.propertyPrices = formBuilder.group({
        venta: [''],
        rentaL: [''],
        rentaV: [''],


        moneda: [''],
        precio: [''],
        monedaR: [''],
        precioM: [''],
        monedaV: [''],
        precioV: [''],
      });
      this.icon = 'md-arrow-dropdown';
      this.edit = true;
      this.text.panel1 = 'Actualizar';
      this.view.panel1 = false;


      this.view.panel1 = true;
      this.view.panel2 = true;
      this.view.panel3 = true;
      this.view.panel4 = true;
      this.view.panel5 = true;
      this.view.panel6 = true;


      this.loadParams();
      var $this = this;
      setTimeout(function() {

        $this.view.panel1 = false;
        $this.view.panel2 = false;
        $this.view.panel3 = false;
        $this.view.panel4 = false;
        $this.view.panel5 = false;
        $this.view.panel6 = false;


      }, 1);
  







    } else {
      this.update = false;
      this.propertyPrices = formBuilder.group({
        venta: ['', [Validators.requiredTrue]],
        rentaL: ['', [Validators.requiredTrue]],
        rentaV: ['', [Validators.requiredTrue]],


        moneda: [''],
        precio: [''],
        monedaR: [''],
        precioM: [''],
        monedaV: [''],
        precioV: [''],
      });
      // //Nueva propiedad
      this.icon = 'md-checkbox';
      this.data.ml = '';
      this.data.m2c = '';
      this.data.cajones = '';
      this.data.onlineW = false;
      this.data.venta = false;
      this.data.rentaL = false;
      this.data.rentaV = false;

    }
  }

  getAgents(office) {
    this.user.getAgents( office)
  }

  selectedTypeProperty(typeproperty){



    if (typeproperty == 4 || typeproperty == 7){
      this.propertyDetail.controls['ml'].setValidators(Validators.required);

      this.propertyDetail.controls['m2c'].clearAsyncValidators();
      this.propertyDetail.controls['m2c'].clearValidators();
      this.propertyDetail.controls['m2c'].disable();
      this.propertyDetail.controls['m2c'].enable();
      this.requiredLabelml='*';
      this.requiredLabelm2c = '';

    } else {
      this.propertyDetail.controls['m2c'].setValidators(Validators.required);
      this.propertyDetail.controls['ml'].clearAsyncValidators();
      this.propertyDetail.controls['ml'].clearValidators();
      this.propertyDetail.controls['ml'].disable();
      this.propertyDetail.controls['ml'].enable();
      this.requiredLabelm2c = '*';
      this.requiredLabelml = '';

    }

  }
  
  selectedOperation(){
    
    /**No preguntes, solo gosalo */
    /**pd: Total de tiempo tratando de entender:  */
    if (this.data.venta || this.data.rentaL || this.data.rentaV  ){
      this.propertyPrices.controls['venta'].clearAsyncValidators();
      this.propertyPrices.controls['rentaL'].clearAsyncValidators();
      this.propertyPrices.controls['rentaV'].clearAsyncValidators();

      this.propertyPrices.controls['venta'].clearValidators();
      this.propertyPrices.controls['rentaL'].clearValidators();
      this.propertyPrices.controls['rentaV'].clearValidators();

      this.propertyPrices.controls['venta'].disable();
      this.propertyPrices.controls['rentaL'].disable();
      this.propertyPrices.controls['rentaV'].disable();

      this.propertyPrices.controls['venta'].enable();
      this.propertyPrices.controls['rentaL'].enable();
      this.propertyPrices.controls['rentaV'].enable();


    }else{
      this.propertyPrices.controls['venta'].setValidators(Validators.requiredTrue);
      this.propertyPrices.controls['rentaL'].setValidators(Validators.requiredTrue);
      this.propertyPrices.controls['rentaV'].setValidators(Validators.requiredTrue);
    }

    if (this.data.rentaL) {
      this.propertyPrices.controls['precioM'].setValidators(Validators.required);
      this.propertyPrices.controls['monedaR'].setValidators(Validators.required);
      this.propertyPrices.controls['precioM'].enable();
      this.propertyPrices.controls['monedaR'].enable();
    }else{
      this.propertyPrices.controls['precioM'].disable();
      this.propertyPrices.controls['monedaR'].disable();
    }

    if (this.data.rentaV) {
      this.propertyPrices.controls['monedaV'].setValidators(Validators.required);
      this.propertyPrices.controls['precioV'].setValidators(Validators.required);
      this.propertyPrices.controls['monedaV'].enable();
      this.propertyPrices.controls['precioV'].enable();
    }else{

      this.propertyPrices.controls['monedaV'].disable();
      this.propertyPrices.controls['precioV'].disable();
    }

    if (this.data.venta) {
      this.propertyPrices.controls['moneda'].setValidators(Validators.required);
      this.propertyPrices.controls['precio'].setValidators(Validators.required);
      this.propertyPrices.controls['precio'].enable();
      this.propertyPrices.controls['moneda'].enable();
    }else{
      this.propertyPrices.controls['moneda'].disable();
      this.propertyPrices.controls['precio'].disable();
    }
 
  }

  /**
   * Actualiza el input select de Municipios
   */
  updateTownship(id){
    this.inputs.getTownship(id);
  }

  /**
   * Actualiza el input select de Colonias
   */
  updateSuburb(id) {
    this.inputs.getSuburb(id);
  }


  /**
   * Carga los valores de la propiedad que va ser editada.
   */
  loadParams() {

    this.propertyid     = this.properties.property.folio;

    //Datos generales
    this.data.tipoProp = this.properties.property.typepropertyid;

    this.data.propiedad = this.properties.property.propertyNameEs;
    this.data.onlineW = parseInt(this.properties.property.onlineweb);
    this.data.keyword   = this.properties.property.keyword;
    this.data.webEsp = this.properties.property.descriptionEs;

    this.data.office = this.properties.property.officeidsold;
    this.data.agent = this.properties.property.agent.agentId;

   //RENTA
    this.data.rentaL  = parseInt(this.properties.property.longtermrental);
    this.data.precioM = this.properties.property.pricepermonth;
    this.data.monedaR = this.properties.property.currencylongmonthid;

    //RENTA VACACIONAL
    this.data.rentaV  = parseInt(this.properties.property.vacation_rental);
    this.data.precioV = this.properties.property.holiday_price;
    this.data.monedaV = this.properties.property.vacation_rental_currency;
    
    // VENTA
    this.data.venta   = parseInt(this.properties.property.sold);
    this.data.precio  = this.properties.property.price;
    this.data.moneda  = this.properties.property.currencyid;

    //ubicación
    this.data.direccion = this.properties.property.address;
    this.data.city      = this.properties.property.city;
    this.data.state     = this.properties.property.stateid;
    this.data.suburb    = this.properties.property.idcolonia;
    this.data.township  = this.properties.property.idmunicipio;

    //Interiores
    this.data.recamaras   = this.properties.property.bedrooms;
    this.data.banios      = this.properties.property.bathrooms;
    this.data.mediobanios = this.properties.property.middlebathrooms;

    //características 
    this.data.m2c       = this.properties.property.m2c;
    this.data.ml        = this.properties.property.mlot;
    this.data.cajones   = this.properties.property.num_parking;



    this.updateTownship(this.data.state);
    this.updateSuburb(this.data.township);
    this.getAgents(this.data.office );
  }

  saveProperty(form) {
    
    this.text.panel1    = 'Guardando...';
    this.spinner.panel1 = true;

    this.propertyGeneral.invalid 
    this.propertyPrices.invalid 
    this.propertyLocation.invalid 
    this.propertyInterior.invalid 
    this.propertyDetail.invalid 

       
    var propertyGeneral   = this.propertyGeneral.getRawValue();
    var propertyPrices    = this.propertyPrices.getRawValue();
    var propertyLocation  = this.propertyLocation.getRawValue();
    var propertyInterior  = this.propertyInterior.getRawValue();
    var propertyDetail    = this.propertyDetail.getRawValue();
    var pathProperties    = '';


    Object.keys(propertyGeneral).forEach(function (key) {
      pathProperties += '&' + key + '=' + propertyGeneral[key];
    });

    Object.keys(propertyPrices).forEach(function (key) {
      pathProperties += '&' + key + '=' + propertyPrices[key];
    }); 
    Object.keys(propertyLocation).forEach(function (key) {
      pathProperties += '&' + key + '=' + propertyLocation[key];
    });
    Object.keys(propertyInterior).forEach(function (key) {
      pathProperties += '&' + key + '=' + propertyInterior[key];
    });
    Object.keys(propertyDetail).forEach(function (key) {
      pathProperties += '&' + key + '=' + propertyDetail[key];
    });


    //Si esta en modo editar, se adjunta el propertyid
    if(this.edit){
      pathProperties +='&propertyid=' + this.propertyid;
    }
    
    var promise = this.properties.add(pathProperties);


    Promise.all([promise]).then(data => {

      // data[0][0] : false -> error, true -> exito 
      // data[0][1] : Folio de la pro piedad en caso de exito

      if(data[0][0]){

        this.propertyid     = data[0][1];
        this.spinner.panel1 = false;
        var $this           = this; 

        // Añade la propiedad creada al string de folios del usuario
        this.user.userProperties = this.user.userProperties + ', ' + this.propertyid;
        
        // Se cargan todas las propiedades del usuario, incluyendo la nueva agregada
        this.properties.getUserProperties();

        if(!this.edit){          
          this.icons.panel1 = true;
          this.text.panel1  = '';                    
        }else{
          this.text.panel1 = 'Actualizar';
        }

        this.alertProperty = this.alertCtrl.create({
          title: '¡La propiedad guardo correctamente!',
          buttons: ['Aceptar']
        });

        this.alertProperty.present();
        this.viewCtrl.dismiss();

      }else{

        this.alertProperty = this.alertCtrl.create({
          title: 'Ocurrio un problema, si el problema persiste contacta con Soporte.',
          buttons: ['Aceptar']
        });

        this.alertProperty.present();


        this.spinner.panel1 = false;
        this.text.panel1    = 'Guardar';
        this.viewCtrl.dismiss();

      }

    });

  }

  process(type) {
 

    if (this.isReadyToSave) {

      this.uploadImage(type);

    } else {

      this.getPicture();
    }

  }

  uploadImage(type) {

    //Crear un providr para subida de archivos, no estaria nada mal.

    var doc = '';
    var m   = '';


    if (type == 1) {

      this.spinner.panel6 = true;
      doc = 'image';
      m   = 'upProperty';
      
    }else{

      this.spinner.panel3 = true;
      doc = 'file';
      m   = 'udProperty';
    }

    var targetPath = this.propertyImages.controls['profilePic'].value;
    var fileTransfer : FileTransferObject = this.transfer.create();

    var options = {
      fileKey:      doc,
      fileName:     this.filename,
      chunkedMode:  false,
      mimeType:     "multipart/form-data",
      params: {
        'm'     : m,
        'user'  : this.user.userInfo.userid,
        'token' : this.user.userToken,
        'folio' : this.propertyid,
        'type'  : 1
      }
    };


    var $this = this;
    
  
    fileTransfer.upload(targetPath, this.api.urlRoot, options).then(data => {



      if (data.responseCode == 200) {
        var response = JSON.parse(data.response);



        JSON.parse(data.response)

        if (response.status == 200) {
          //Actualiza las propiedades y sus imagenes
          this.properties.getUserProperties();

          this.alertProperty = this.alertCtrl.create({
            title: '¡Imagen subida correctamente!',
            buttons: ['Aceptar']
          });

          this.alertProperty.present();
  
        } else {

          this.api.alert(601);        
        }     
      } else {     
        this.api.alert(600);
      }

      if (type == 1) {
        this.spinner.panel6 = false;
      } else {
        this.spinner.panel3 = false;
      }

      $this.isReadyToSave = false;
     this.openPanel6();

    }, err => {
      this.api.alert(602);
    });
  
  }

  getPicture() { 



    let actionSheet = this.actionSheetCtrl.create({
      title   : 'Selecciona la fuente',
      buttons : [{
                    text    : 'Galería',
                    handler : () => {

                      this.fileInput.nativeElement.click();    

                    }
                },{
                    text: 'Cámara',  
                    handler: () => {
                      this.camera.getPicture({ destinationType: this.camera.DestinationType.DATA_URL, targetWidth: 1600, correctOrientation: true }).then((data) => {
                  
                        this.propertyImages.patchValue({ 'profilePic': 'data:image/jpg;base64,' + data });
                        var d = new Date();
                        var n = d.getTime();
                        this.filename = 'foto_'+n+'.jpg';
                        this.gallery.push('data:image/jpg;base64,' + data );
                        this.view2.color = 'green';
                        this.view2.text = 'Cargar foto';
                      }, (err) => {
                        alert('No se pudo seleccionar la imagen');
                      });
                    }
                },{
                    text: 'Cancelar',
                    role: 'cancel'
                }]
      });

    actionSheet.present();
  }

  processWebImage(event) {

    let reader = new FileReader();


    reader.onload = (readerEvent) => {
     

      var fullPath      = this.fileInput.nativeElement.value;   

      var startIndex    = (fullPath.indexOf('\\') >= 0 ? fullPath.lastIndexOf('\\') : fullPath.lastIndexOf('/'));


      var filename      = fullPath.substring(startIndex);


      this.view2.color  = 'green';

      this.view2.text   = 'Cargar foto';


      if (filename.indexOf('\\') === 0 || filename.indexOf('/') === 0) {
          this.filename = filename.substring(1);


      }

      let imageData     = (readerEvent.target as any).result;

      this.propertyImages.patchValue({ 'profilePic': imageData });

    };

    reader.readAsDataURL(event.target.files[0]);

  }

  getProfileImageStyle() {
    return 'url(' + this.propertyImages.controls['profilePic'].value + ')';
  }

  goDocuments(){
    this.color.panel2 = 'green';
    this.spinner.panel2 = false;
    this.view.panel3 = true;
    this.view.panel2 = false;
  }


  checkForms(){
    if(  this.propertyGeneral.invalid || this.propertyPrices.invalid ||   this.propertyLocation.invalid ||  this.propertyInterior.invalid ||  this.propertyDetail.invalid  ){
      
      return true;
    }else{

      return false;

    }
  }



  finish(){

    this.color.panel3 = 'green';
    this.spinner.panel3 = false;
    this.view.panel3 = false;
    this.view.panel4 = true;

  }

  goMyproperties(){
    this.viewCtrl.dismiss();
    this.navCtrl.push('MyPropertiesPage');
  }




  openPanel1(){
    if (this.view.panel1){
      this.view.panel1 = false;

    }else{
     
        this.view.panel1 = true;
        this.view.panel2 = false;
        this.view.panel3 = false;
        this.view.panel4 = false;
        this.view.panel5 = false;
        this.view.panel6 = false;

      

    }

 

  }

  openPanel2() {



    if (this.view.panel2) {
      this.view.panel2 = false;

    } else {
      
        this.view.panel1 = false;
        this.view.panel2 = true;
        this.view.panel3 = false;
        this.view.panel4 = false;
        this.view.panel5 = false;
        this.view.panel6 = false;
      
    }

   
  }
 

  openPanel3() {

    if (this.view.panel3) {
      this.view.panel3 = false;

    } else {
     
        this.view.panel1 = false;
        this.view.panel2 = false;
        this.view.panel3 = true;
        this.view.panel4 = false;
        this.view.panel5 = false;
        this.view.panel6 = false;



        // this.form2.patchValue({ 'profilePic': '' });
        // this.isReadyToSave = false;
      
    }


 

  }

  openPanel4(){

    if (this.view.panel4) {
      this.view.panel4 = false;

    } else {
    
        this.view.panel1 = false;
        this.view.panel2 = false;
        this.view.panel3 = false;
        this.view.panel4 = true;
        this.view.panel5 = false;
        this.view.panel6 = false;
      
    }

  
  }


  openPanel5() {

    if (this.view.panel5) {
      this.view.panel5 = false;

    } else {
    
        this.view.panel1 = false;
        this.view.panel2 = false;
        this.view.panel3 = false;
        this.view.panel4 = false;
        this.view.panel5 = true;
        this.view.panel6 = false;
      
    }


  }

  openPanel6() {

    if (this.view.panel6) {
      this.view.panel6 = false;

    } else {

    
        this.view.panel1 = false;
        this.view.panel2 = false;
        this.view.panel3 = false;
        this.view.panel4 = false;
        this.view.panel5 = false;
        this.view.panel6 = true;

        this.propertyImages.patchValue({ 'profilePic': '' });
        this.isReadyToSave = false;
      
    }




  }


  detail(folio) {
    this.properties.query(this.propertyid).subscribe(data => {
      if(data.status == 200){
        if(data.json().status== 200){
          this.properties.property = data.json().data;
          this.navCtrl.push('PropertyDetailPage', folio);
        }else{
          this.api.alert(801);
        }
      }else{
        this.api.alert(800);
      } 
    });
  }

}


